import { prisma } from "../../lib/db";
import Link from "next/link";
import { notFound } from "next/navigation";

export default async function UserProfile({ params }: { params: Promise<{ id: string }> }) {
  const { id } = await params;
  
  // Next.js fetches this ON THE SERVER. 
  // The user never sees a "loading" state for the data.
  const user = await prisma.user.findUnique({
    where: { id: parseInt(id) },
  });

  if (!user) {
    notFound(); // Built-in Next.js 404
  }

  return (
    <main className="min-h-screen bg-[#09090b] text-zinc-100 p-12">
      <div className="max-w-2xl mx-auto">
        <Link href="/" className="text-zinc-500 hover:text-cyan-500 text-sm mb-8 inline-block transition-colors">
          ← Back to Directory
        </Link>
        
        <div className="bg-zinc-900/50 p-10 rounded-3xl border border-zinc-800 shadow-2xl border-l-4 border-l-cyan-500">
          <div className="flex items-center gap-4 mb-6">
            <div className="w-16 h-16 bg-cyan-500 rounded-2xl flex items-center justify-center text-black text-2xl font-black">
              {user.name?.[0] || "?"}
            </div>
            <div>
              <h1 className="text-3xl font-bold">{user.name}</h1>
              <p className="text-zinc-500">{user.email}</p>
            </div>
          </div>

          <div className="space-y-4">
            <h2 className="text-xs font-bold text-zinc-600 uppercase tracking-widest">About User</h2>
            <p className="text-zinc-300 leading-relaxed bg-zinc-950 p-6 rounded-2xl border border-zinc-800">
              {user.bio}
            </p>
          </div>

          <div className="mt-8 pt-8 border-t border-zinc-800 text-[10px] text-zinc-700 uppercase font-black tracking-[0.2em]">
            System Record ID: {user.id} • Registered {new Date(user.createdAt).toDateString()}
          </div>
        </div>
      </div>
    </main>
  );
}